import React from 'react';
import './Search.css';
import { Redirect } from "react-router-dom"

class Search extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            placename: "",
            userGood: false
        }
    }

    onSubmit = (event) => {
        if(this.state.placename.trim().length === 0){
            this.setState({userGood: true});
        }
        event.preventDefault();
    }

    whenChanged = (event) => {
        const value = event.target.value;
        this.setState({placename: value});
    }

    render(){    

        let from = {pathname: '/Listing', state: {place: this.state.placename}}

        if (this.state.userGood) {
            return(
                <Redirect to={from} />
            );

        } else {
            return (
                <div className="SearchDiv">
                    <h1 className="header">YELPERS!!!</h1>
                    <form className="SearchButton" onSubmit={this.onSubmit}>
                        {/* <label>Place:  </label> */}
                        {/* <input value={this.state.placename}
                            type="text"
                            name="placename"
                            onChange={this.whenChanged}
                        ></input> */}
                        <button type="submit">Search</button>
                    </form>
                </div>
            );
        }
        
    }
}

export default Search;