import React from 'react';
import './App.css';
import {
  BrowserRouter,
  Route,
  Switch,
} from "react-router-dom"
import Search from './Components/Search';
import Listing from './Components/Listing';

function App() {
  return (
    <BrowserRouter >
      <Switch>
        <Route path='/Listing' render={props => <Listing {...props} />}>
        </Route>
        <Route path='/'>
          <Search />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
