import React from 'react';
import './Listing.css';

class Listing extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: "",
            newname: "",
            newlocation: "",
            newreview: "",
            deletename: "",
            addreview: "",
            addname: "",
            near: "",
            entries: [],
        }
    }

    handleAddChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    handleAddSubmit = (event) => {
        //alert(this.state.newname+","+this.state.newlocation+","+this.state.newreview);
        if (this.state.newname.length > 0 && this.state.newlocation.length > 0) {
            let api = 'https://jasontbaker-mynearbyplaces.herokuapp.com/addplace';
            let data = {name: this.state.newname, place: this.state.newlocation, review: this.state.newreview};
            fetch(api, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            }).then(x => x.json()).then(y => console.log(y)).catch(e => console.log(e));
        }
        event.preventDefault();
    }

    // handleReviewChange = (event) => {
    //     this.setState({
    //         [event.target.name]: event.target.value
    //     });
    // }

    handleReviewSubmit = (event) => {
        let api = 'https://jasontbaker-mynearbyplaces.herokuapp.com/allplaces';
        fetch(api).then(x => x.json()).then(out => {
            let n;
            let p;
            let r;
            let e = [];
            console.log(out);
            for(let i=0;i<out.length;i++){
                n = out[i].name;
                p = out[i].place;
                r = out[i].review;
                let temp = {id: i, name: n, location: p, reviews: r};
                console.log(temp);
                e.push(temp);
            }
            this.setState({entries: e});
        }).catch(e => console.log(e));
        event.preventDefault();
    }

    handleDeleteChange = (event) => {
        this.setState({ deletename: event.target.value });
    }

    handleDeleteSubmit = (event) => {
        if (this.state.deletename.length > 0) {
            let api = 'https://jasontbaker-mynearbyplaces.herokuapp.com/deleteplace';
            let data = {name: this.state.deletename};
            fetch(api, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            }).then(x => x.json()).then(y => console.log(y)).catch(e => console.log(e));
        }
        event.preventDefault();
    }

    handleNearChange = (event) => {
        this.setState({ near: event.target.value });
    }

    handleNearSubmit = (event) => {
        if (this.state.near.length > 0) {
            let api = 'https://jasontbaker-mynearbyplaces.herokuapp.com/findbyplace/'+this.state.near;
            fetch(api).then(x => x.json()).then(out => {
                let n;
                let p;
                let r;
                let e = [];
                for(let i=0;i<out.length;i++){
                    n = out[i].name;
                    p = out[i].place;
                    r = out[i].review;
                    let temp = {id: i, name: n, location: p, reviews: r};
                    console.log(temp);
                    e.push(temp);
                }
                this.setState({entries: e});
            }).catch(e => console.log(e));
        }
        event.preventDefault();
    }
    handleSearchChange = (event) => {
        this.setState({ name: event.target.value });
    }

    handleSearchSubmit = (event) => {
        console.log(this.state.name);
        if (this.state.name.length > 0) {
            let api = 'https://jasontbaker-mynearbyplaces.herokuapp.com/findbyname/'+this.state.name;
            fetch(api).then(x => x.json()).then(out => {
                let n;
                let p;
                let r;
                let e = [];
                for(let i=0;i<out.length;i++){
                    console.log(out[i]);
                    n = out[i].name;
                    p = out[i].place;
                    r = out[i].review;
                    let temp = {id: i, name: n, location: p, reviews: r};
                    console.log(temp);
                    e.push(temp);
                }
                this.setState({entries: e});
            }).catch(e => console.log(e));
        }
        event.preventDefault();
    }

    body = (n) => {
        const { entries } = this.state;
        return (
            <div className="Content">
                {entries.length > 0 ?
                    <div className="entry">
                        <ul>
                            {entries.map((item) => (
                                <li key={item.id}>
                                    <div>Name: {item.name}</div>
                                    <div>Location: {item.location}</div>
                                    <div>Review: "{item.reviews}"</div>
                                    <div>__________________________</div>
                                </li>
                            ))}
                        </ul>
                    </div> : ''};
            </div>
        );
    }

    render() {

        let n = '';
        if (this.props.location) {
            if (this.props.location.state) {
                if (this.props.location.state.place) {
                    n = this.props.location.state.place;
                }
            }
        }

        return (
            <div className="Back">
                <div className="Top">Welcome to YELPERS!!!</div>
                <div className="Button">
                    <form className="ADDING" onSubmit={this.handleAddSubmit}>
                        <label>Name:<br/> <input type="text" value={this.state.newname}
                            name="newname" onChange={this.handleAddChange} /></label>
                        <br />
                        <label>
                            Location: <br/><input type="text" value={this.state.newlocation}
                                name="newlocation" onChange={this.handleAddChange} />
                        </label>
                        <br />
                        <label>
                            Review:<br/> <input type="text" value={this.state.newreview}
                                name="newreview" onChange={this.handleAddChange} />
                        </label>
                        <br />
                        <input type="submit" value="Add Location" />
                    </form>
                    <br/>
                    <form className="DELETE" onSubmit={this.handleDeleteSubmit}>
                        <label>Name:<br/> <input type="text" value={this.state.newdelete}
                            name="deletename" onChange={this.handleDeleteChange} /></label>
                        <br />
                        <input type="submit" value="Delete Location" />
                        <br/>Use search to see Update
                    </form>
                    <br/>
                    <form className="NEAR" onSubmit={this.handleNearSubmit}>
                        <label>Your Location: <br/><input type="text" value={this.state.near}
                            name="near" onChange={this.handleNearChange} /></label>
                        <br />
                        <input type="submit" value="Look Nearby" />
                    </form>
                    <br/>
                    <form className="Search" onSubmit={this.handleSearchSubmit}>
                        <label>Search:<br/> <input type="text" value={this.state.name}
                            name="near" onChange={this.handleSearchChange} /></label>
                        <br />
                        <input type="submit" value="Search Again" />
                    </form>
                    <br/>
                    <form className="REVIEW" onSubmit={this.handleReviewSubmit}>
                        {/* <label>Name:<br/> <input type="text" value={this.state.addname}
                            name="addname" onChange={this.handleReviewChange} /></label>
                        <br />
                        <label>Review:<br/> <input type="text" value={this.state.addreview}
                            name="addreview" onChange={this.handleReviewChange} /></label>
                        <br /> */}
                        <input type="submit" value="All Places" />
                    </form>
                </div>
                <div className="Listing"> Found {this.body(n)}</div>
            </div>
        );


    }
}

export default Listing;